body{
    margin: 0;
    /* padding: 0; */
    background-color: #333;
}
.header {
   display:flex;
   height: 20vh;
   justify-content:center;
   align-items:center;
   flex-wrap:wrap;
   background-color: #333;
   padding: 1rem 1.5rem;
   
   

}
select, input{
    padding: .45rem 1rem;
    background-color: #eeeeee;
    outline:none;
}

.form-grp{
    display: flex;
    font-weight : 500;
    flex-direction: column;
    margin: .5rem;
}
.form-grp > label{
    color: #eeeeee;
    text-align: center;
    font-size: .75rem;
    margin-bottom: .5rem;
}

.btn {
    background-color: #eeeeee;
    color : #6a2c70;
    margin-top: 1.43rem;
    font-weight: 700;
    padding: .5rem 1rem;
    border-radius: .6rem;
    border: none;
    cursor: pointer;
    outline: none;
    transition: all .3s;
}
.btn:hover{
    background-color: #eb2f64;
    color: white;
}
.btn:active{
    transform: scale(1.2);
}

.container {
    max-width : 15000px;
    margin :1rem 2rem;
}

.card-grid{
    display: grid;
    align-items: center;
    grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
    gap : 2rem;
}

.card {
    display :flex;
    justify-content :center;
    align-items : center;
    position : relative;
    border-radius: .25rem;
    box-shadow : 0 2px 2px 3px #6a2c7052;
    background-color: #eeeeee;
    transition: transform .4s;
    transform-style: preserve-3d;
    cursor : pointer;
    transform: perspective(1000px) rotateY(var(--rotate-y,0)) translateY(var(--translate-y,0));
}
.card:hover{
    --translate-y : -8px;
    box-shadow : 0 2px 2px 3px #6a2c7052;
  
}
.card.flip{
    --rotate-y : 180deg;
}

.card .front,
.card .back{
    position: absolute;
    padding: 1rem;
    backface-visibility: hidden;
}

.card .back{
    transform : rotateY(180deg);
    color : #eeeeee;
    background-color: #eb2f64;
    font-weight: 500;
}
.back{
    display :flex;
    /* flex-direction : column; */
    align-items: center;
    justify-content : center;

    height: 90%;
    background-clip: inherit;
    width:93%;
    border-radius: .25rem;
}


.card-options {
    margin-top: .5rem
}
.card-option {
    margin-top: .25rem;
    color: #555;
    font-size:.75rem;
}
.card-option:first-child {
    margin-top:0;
}